'use client';

import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

interface DriverRankingChartProps {
  data: {
    name: string;
    deliveries: number;
    onTimeRate: number;
    company: string;
  }[];
}

export default function DriverRankingChart({ data }: DriverRankingChartProps) {
  const getCompanyColor = (company: string) => {
    const colors = {
      'INNOVATION': 'hsl(var(--chart-1))',
      'GARBERG': 'hsl(var(--chart-2))',
      'FULLFLEX': 'hsl(var(--chart-3))',
      'PACHECO': 'hsl(var(--chart-4))',
      'FRATELLI': 'hsl(var(--chart-5))',
      'SANPOR': 'hsl(var(--chart-1))',
      'PRC': 'hsl(var(--chart-2))'
    };
    return colors[company as keyof typeof colors] || 'hsl(var(--chart-1))';
  };

  const CustomBar = (props: any) => {
    const { payload } = props;
    return (
      <Bar 
        {...props} 
        fill={getCompanyColor(payload?.company)}
        radius={[0, 4, 4, 0]}
      />
    );
  };

  return (
    <ResponsiveContainer width="100%" height={400}>
      <BarChart
        data={data}
        layout="horizontal"
        margin={{ top: 20, right: 30, left: 80, bottom: 5 }}
      >
        <CartesianGrid strokeDasharray="3 3" stroke="hsl(var(--border))" />
        <XAxis 
          type="number" 
          stroke="hsl(var(--muted-foreground))"
          fontSize={12}
        />
        <YAxis 
          type="category" 
          dataKey="name" 
          stroke="hsl(var(--muted-foreground))"
          fontSize={11}
          width={75}
        />
        <Tooltip 
          contentStyle={{
            backgroundColor: 'hsl(var(--card))',
            border: '1px solid hsl(var(--border))',
            borderRadius: '8px',
            color: 'hsl(var(--card-foreground))'
          }}
          formatter={(value, name, props) => [
            `${value} entregas`,
            `${props.payload.company} - ${props.payload.onTimeRate}% pontualidade`
          ]}
        />
        <Bar 
          dataKey="deliveries" 
          shape={<CustomBar />}
        />
      </BarChart>
    </ResponsiveContainer>
  );
}
