'use client';

interface RouteHeatmapProps {
  data: {
    origin: string;
    destination: string;
    volume: number;
    maxVolume: number;
  }[];
}

export default function RouteHeatmap({ data }: RouteHeatmapProps) {
  const getIntensityColor = (volume: number, maxVolume: number) => {
    const intensity = volume / maxVolume;
    if (intensity >= 0.8) return 'bg-red-500';
    if (intensity >= 0.6) return 'bg-orange-500';
    if (intensity >= 0.4) return 'bg-yellow-500';
    if (intensity >= 0.2) return 'bg-blue-500';
    return 'bg-gray-300';
  };

  const getIntensityOpacity = (volume: number, maxVolume: number) => {
    const intensity = volume / maxVolume;
    return Math.max(0.2, intensity);
  };

  return (
    <div className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {data.map((route, index) => (
          <div
            key={index}
            className="relative p-4 rounded-lg border border-border overflow-hidden"
          >
            <div
              className={`absolute inset-0 ${getIntensityColor(route.volume, route.maxVolume)}`}
              style={{ opacity: getIntensityOpacity(route.volume, route.maxVolume) }}
            />
            <div className="relative z-10">
              <div className="flex items-center justify-between mb-2">
                <span className="text-sm font-medium corporate-subheading">
                  {route.origin} → {route.destination}
                </span>
                <span className="text-xs bg-background/80 px-2 py-1 rounded">
                  {route.volume}
                </span>
              </div>
              <div className="w-full bg-background/20 rounded-full h-2">
                <div
                  className="bg-foreground/60 h-2 rounded-full transition-all duration-300"
                  style={{ width: `${(route.volume / route.maxVolume) * 100}%` }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>
      
      <div className="flex items-center justify-center space-x-4 text-xs text-muted-foreground">
        <div className="flex items-center space-x-2">
          <div className="w-3 h-3 bg-gray-300 rounded"></div>
          <span>Baixo</span>
        </div>
        <div className="flex items-center space-x-2">
          <div className="w-3 h-3 bg-blue-500 rounded"></div>
          <span>Moderado</span>
        </div>
        <div className="flex items-center space-x-2">
          <div className="w-3 h-3 bg-yellow-500 rounded"></div>
          <span>Alto</span>
        </div>
        <div className="flex items-center space-x-2">
          <div className="w-3 h-3 bg-orange-500 rounded"></div>
          <span>Muito Alto</span>
        </div>
        <div className="flex items-center space-x-2">
          <div className="w-3 h-3 bg-red-500 rounded"></div>
          <span>Crítico</span>
        </div>
      </div>
    </div>
  );
}
