'use client';

import { PieChart, Pie, Cell, ResponsiveContainer } from 'recharts';

interface GaugeChartProps {
  value: number; // Percentage (0-100)
  title: string;
}

export default function GaugeChart({ value, title }: GaugeChartProps) {
  const data = [
    { name: 'Completed', value: value },
    { name: 'Remaining', value: 100 - value }
  ];

  const COLORS = ['hsl(var(--chart-2))', 'hsl(var(--muted))'];

  const getColor = (value: number) => {
    if (value >= 90) return 'hsl(var(--chart-2))'; // Green
    if (value >= 70) return 'hsl(var(--chart-4))'; // Yellow
    return 'hsl(var(--destructive))'; // Red
  };

  return (
    <div className="flex flex-col items-center">
      <ResponsiveContainer width="100%" height={200}>
        <PieChart>
          <Pie
            data={data}
            cx="50%"
            cy="50%"
            startAngle={180}
            endAngle={0}
            innerRadius={60}
            outerRadius={80}
            paddingAngle={0}
            dataKey="value"
          >
            <Cell fill={getColor(value)} />
            <Cell fill="hsl(var(--muted))" />
          </Pie>
        </PieChart>
      </ResponsiveContainer>
      
      <div className="text-center -mt-8">
        <div className="text-3xl font-bold corporate-heading" style={{ color: getColor(value) }}>
          {value}%
        </div>
        <div className="text-sm text-muted-foreground corporate-subheading">
          {title}
        </div>
      </div>
    </div>
  );
}
